<template>
  <div class="myprize-wrap">
    <Loadmore v-if="!!prizeList.length"
              class="prize-list"
              :bottom-method="loadBottom"
              :bottom-all-loaded="allLoaded"
              ref="loadmore"
              bottomPullText="">
      <div class="list-item-box"
           v-for="(item, index) in prizeList"
           :key="index">
        <div class="prize-img-box">
          <div class="img-box flex">
            <img v-if="item.ticketImg" :src="img_baseUrl+item.ticketImg" alt="">
          </div>
          <div class="person-info flex">
            <span class="info-desc">中奖人</span>
            <div class="line-vertical"></div>
            <img v-if="item.headImgUrl" :src="item.headImgUrl" alt="">
            <img v-else src="../clock/imgs/head-img.png" alt="">
            <span class="info-nickname one-txt-cut">{{item.nickName}}</span>
          </div>
        </div>

        <div class="prize-detail-text">
          <div class="prize-name txt-cut">{{item.ticketName}}</div>
          <div class="price-box">
            <span class="text-left">市场价值：</span>
            <span class="text-price">{{item.marketPrice}}</span>
            <span class="text-unit">&nbsp;元</span>
          </div>
          <div class="time-box flex-start">
            <span class="price-time">{{item.earnTime | formatTime}}期</span>
            <ButtonM v-if="item.cornerTag" class="prize-tag">当期</ButtonM>
          </div>
        </div>
      </div>
      <div v-if="!!prizeList.length && allLoaded" class="no-more">无更多数据</div>
    </Loadmore>

    <NoRecord v-if="noRecord">还没有任何中奖记录呦～</NoRecord>
  </div>
</template>

<script>
  import ButtonM from '../../components/ButtonM/ButtonM';
  import NoRecord from './conponents/NoRecord/NoRecord';
  import {prizeRecord} from "../../api/daylottery";
  import { Loadmore } from 'mint-ui';
  import {baseUrl} from "../../utils/request";
  import {isToday} from "../../utils/tools";
  import {clog} from "../../api/punchin";
  export default {
    name: "past-prize",
    components: {ButtonM, Loadmore, NoRecord},
    data() {
      return {
        // {
        // "earnTime": '中奖期数', // "ticketName": '奖项名称',   // "ticketFullName": '奖项全称',
        // "ticketImg": '', //"奖项列表图片"// "marketPrice": 市场参考价格,  // "userName": '领奖人姓名',
        // "phoneNum": '领奖人手机号',
        // }
        prizeList: [],
        allLoaded: false,
        bottomStatus: '',
        img_baseUrl: baseUrl.imgURL,
        noRecord: false,
        curPage: 1,
      }
    },
    methods: {
      loadBottom() {
        console.log('loadBottom');
        if(this.loadBottom.prevent) return
        this.loadBottom.prevent = true
        this.bottomStatus = 'loading'
        this.curPage++
        this.queryList().then(() => {
          this.$refs.loadmore.onBottomLoaded();
          this.loadBottom.prevent = false
        })

      },
      queryList() {
        const data = {
          currPeriodFlag: 0,
          currentPage: this.curPage,
          count: 20
        }
        return prizeRecord(data).then(res => {
          console.log(res);
          const {result, reply} = res
          if(result.businessCode === '0') {
            reply.map(item => {
              if(isToday(new Date(item.earnTime))) {
                item.cornerTag = '当期'
              }
            })
            this.prizeList = this.prizeList.concat(reply)
            if(!this.prizeList.length) this.noRecord = true
            if(reply.length < 20) this.allLoaded = true
          } else if(result.businessCode === '2') {
            console.log('无更多数据');
            if(!this.prizeList.length) this.noRecord = true
            this.allLoaded = true
          }
        })
      }
    },
    filters: {
      formatTime(val) {
        if(!val) return ''
        return val.split(' ')[0].replace(/\-/g,'.')
      }
    },
    created() {
      this.queryList()
      clog('天天抽奖', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'往期奖品页','进入往期奖品页']);

    }
  }
</script>

